@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');

*{
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
}

body {
    background-color:#203452;

}

#header {
    width: 100%;
    height: 1024px;
    background-image: url("../assets/images/header-img.jpg");
    background-position: center;
    background-size: cover;
}

#header img{
    width: 35%;
    height: auto;
    padding-top: 325px;
    padding-left: 135px;
}

#header h1, #company-mission-text h2, #company-mission-text p, #details-text h3, #details-text h4, #details-text p{
    color: whitesmoke;
    font-size: 45px;
    font-family: "Inter", sans-serif;
    font-optical-sizing: auto;
    font-weight: 300;
    font-style: normal;
    text-align: left;
    padding-top: 25px;
    padding-left: 135px;
    padding-right: 50%;
}

.btn-container{
    display: flex;
    padding-top: 45px;
    padding-left: 135px;
    gap: 35px;
}

.cta-button{
    padding: 10px;
    color:whitesmoke;
    background-color: #419B6B;
    font-family: "Inter", sans-serif;
    font-optical-sizing: auto;
    font-weight: 700;
    font-style: normal;
    font-size: 16px;
    letter-spacing: 1px;
    border: none;
    border-radius: 25px;
    min-width: 220px;
    min-height: 55px;
}

#join-button:hover{
    background-color: #23533a;
}

#learn-button{
    background-color: #419b6b00;
    border: 3px solid white;
}

#learn-button a{
    text-decoration: none;
    color: whitesmoke;
}

#learn-button:hover, #learn-button a:hover{
    background-color: white;
    color:#203452;
    border: none;
}

#company-description, #details{
    background-color: white;
}

#company-mission-text h2, #details-text h3{
    color:#203452;
    font-weight: 900;
    font-style: bold;
    padding-top: 65px;
}

#company-mission-text p, #details-text p, #details-text h4{
    color: #203452;
    font-style: normal;
    font-size: 22.5px;
    padding-right: 50%;
}

#company-mission-text p:nth-of-type(3):not(#company-desc-text p:nth-of-type(3)){
    padding-bottom: 65px;
}

#company-desc-text p:nth-of-type(4){
    padding-bottom: 65px;
}

#details-text p:nth-of-type(11){
    padding-bottom: 45px;
}

#company-desc-text h2, #company-desc-text p{
    color: whitesmoke;
    background-color: #203452;
}

#details-text button{
    margin-left: 135px;
    margin-bottom: 65px;
}